}

.gallery-section {
  padding: 60px 0;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

.search-btn:hover, .search-btn:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: -30px;
  display: flex;
}

.gallery__item {
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
}

.gallery-img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
}

.posts-section {
  padding: 100px 0;
}

.posts {
  flex-wrap: wrap;
  gap: 30px 5%;
  margin-bottom: 40px;
  display: flex;
}

.posts__item {
  height: 340px;
  flex-basis: 30%;
  padding: 20px;
  overflow-y: auto;
  box-shadow: 0 0 10px #ccc;
}

.posts__title {
  margin-bottom: 20px;
  font-size: 22px;
}

.posts__text {
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 1.4;
}

.load__more__btn {
  min-width: 200px;
  color: #fff;
  cursor: pointer;
  background-color: #34495e;
  border: none;
  border-radius: 6px;
  margin: 0 auto;
  padding: 14px 4px;
  transition: background-color .25s linear;
  display: block;
}

.load__more__btn.is-hidden {
  display: none;
}

.load-more-btn:hover, .load-more-btn:focus {
  background-color: #2c3e50;
}

